export const isInvalidEmail = (email) =>
  !/^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i.test(
    email,
  );

export const isInvalidPassword = (password) =>
  !/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/.test(password);

export const isInvalidName = (name) => /\d+/.test(name);

export const isEmpty = (value) => !value;

export const checkName = (name) => {
  if (isEmpty(name)) {
    throw new Error(' !');
  }

  if (isInvalidName(name)) {
    throw new Error('   !');
  }
};

export const checkEmail = (email) => {
  if (isEmpty(email)) {
    throw new Error(' !');
  }

  if (isInvalidEmail(email)) {
    throw new Error(' !');
  }
};

export const checkPassword = (password) => {
  if (isEmpty(password)) {
    throw new Error(' !');
  }

  if (isInvalidPassword(password)) {
    throw new Error(
      ` 8      !`,
    );
  }
};

export const checkPasswordConfirmation = (password, passwordConfirmation) => {
  if (isEmpty(passwordConfirmation)) {
    throw new Error('  !');
  }

  if (password !== passwordConfirmation) {
    throw new Error('    !');
  }
};
